'use client';

import React from 'react';

interface Tab {
  id: string;
  label: string;
  icon: string;
}

interface ManagerHeaderProps {
  title?: string;
  onCreateNew?: () => void;
  onRefresh?: () => void;
  showCreateButton?: boolean;
  showRefreshButton?: boolean;
  loading?: boolean;
  tabs?: Tab[];
  activeTab?: string;
  onTabChange?: (tabId: string) => void;
  createButtonText?: string;
  createButtonIcon?: string;
  refreshButtonText?: string;
  refreshButtonIcon?: string;
}

const ManagerHeader: React.FC<ManagerHeaderProps> = ({
  title = "Gesti贸n",
  onCreateNew,
  onRefresh,
  showCreateButton = true,
  showRefreshButton = true,
  loading = false,
  tabs = [],
  activeTab,
  onTabChange,
  createButtonText = "Nuevo",
  createButtonIcon = "fa-plus",
  refreshButtonText = "Actualizar", 
  refreshButtonIcon = "fa-sync-alt"
}) => {
  return (
    <div className="bg-white rounded-lg shadow-md border border-gray-200 mb-6">
      <div className="p-6 border-b border-gray-200">
        <h1 className="text-2xl font-bold text-gray-800 mb-6">{title}</h1>
        
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-4 sm:space-y-0">
          {/* Tabs de navegaci贸n */}
          {tabs.length > 0 && onTabChange && (
            <>
              {tabs.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => onTabChange(tab.id)}
                  className={`px-4 py-2 rounded-md font-medium transition-colors duration-200 ${
                    activeTab === tab.id
                      ? 'text-white bg-blue-600 hover:bg-blue-700'
                      : 'text-white bg-blue-500 hover:bg-blue-600'
                  }`}
                >
                  <i className={`fas ${tab.icon} mr-2`}></i>
                  {tab.label}
                </button>
              ))}
            </>
          )}

          {/* Botones de acci贸n */}
          <div className="flex space-x-3">
            {/* Bot贸n Crear */}
            {showCreateButton && onCreateNew && (
              <button
                onClick={onCreateNew}
                className="flex items-center space-x-2 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors font-medium"
              >
                <i className={`fas ${createButtonIcon}`}></i>
                <span>{createButtonText}</span>
              </button>
            )}

            {/* Bot贸n Actualizar */}
            {showRefreshButton && onRefresh && (
              <button
                onClick={onRefresh}
                disabled={loading}
                className="flex items-center space-x-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-medium"
              >
                <i className={`fas ${refreshButtonIcon} ${loading ? 'animate-spin' : ''}`}></i>
                <span>{loading ? 'Actualizando...' : refreshButtonText}</span>
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManagerHeader;